import { IActionModel, ActionType, IClientData } from '../../../directives/action/models/action.model';
import { ExecuteCmdForm } from '../services/execute.cmd';
import * as _ from 'lodash';

function busy(constructor: Function) {
    constructor.prototype.isBusy = false;

    console.log(constructor);
}

@busy
export class PageExecuteCmdResultController {
    static $inject = ["$scope", "fxAction", "sockets", "$q"];

    cmdId: string;
    cmdClientData: IClientData = {};
    cmdResClientData: IClientData = {};

    deviceClientData: IClientData = {};
    deviceSelected: Array<any> = [];
    command: any;
    filterDeviceSnBind: Function;
    isBusy: boolean;

    constructor(private $scope, private fxAction, private sockets, private $q: angular.IQService) {
        this.$scope.$on("socket:connect", () => {

        });
        this.$scope.$on("socket:events", (event, msg) => {
            if (msg._source.jid !== this.cmdId) {
                return;
            }

            if (!this.cmdResClientData.rows) {
                return this.cmdResClientData.rows = [msg];
            }
            if (!_.some(this.cmdResClientData.rows, (row) => {
                return row._id === msg._id;
            })) {
                this.cmdResClientData.rows.push(msg);
            }
        });
        this.$scope.$on("showExecuteCmdResult", (event, cmdId: string) => {
            this.getCommandResult(cmdId);
        });

        this.filterDeviceSnBind = this.filterDeviceSn.bind(this);
    }

    filterDeviceSn() {
        if (this.deviceSelected.length) {
            return {
                _source: {
                    deviceSn: this.deviceSelected[0]._source.deviceSn
                }
            };
        }

        return null;
    }

    getCommandResult(cmdId: string) {
        this.isBusy = true;
        this.cmdId = cmdId;
        this.$q.all([
            this.fxAction.doAction("executeCmdList", { where: { "query": { "and": [{ "match": { "_id": cmdId } }] } } }),
            this.fxAction.doAction("executeCmdResList", { where: { "query": { "and": [{ "match": { "jid": cmdId } }] } } })
        ]).then((results) => {
            this.fxAction.doDealResult(results[0].actionModel, results[0], this.cmdClientData);
            this.fxAction.doDealResult(results[1].actionModel, results[1], this.cmdResClientData);
        }).then(() => {
            if (this.cmdClientData.rows.length) {
                this.command = this.cmdClientData.rows[0]._source.command;
                this.deviceClientData.rows = this.cmdClientData.rows[0]._source.listip;
                this.deviceClientData.rows.length && (this.deviceSelected = [this.deviceClientData.rows[0]]);
            }
        }).finally(() => {
            this.isBusy = false;
        });
    }
}

export class PageExecuteCmdController {
    static $inject = ["$rootScope", "$stateParams", "$timeout", "materialUtils", "fxAction", "toolbarUtils"];

    key: string;
    isOpen: boolean;
    toolbars: Array<any>;
    actionModel: IActionModel;
    formData: any;
    isBusy: boolean;
    executeResult: any = {};

    constructor(private $rootScope: angular.IRootScopeService, private $stateParams: ng.ui.IStateParamsService, private $timeout, private materilUtils: fx.utils.materialStatic, private fxAction, private toolbarUtils) {
        this.key = ExecuteCmdForm.key;
        this.doInitToolbar();
        this.formData = {
            listIps: []
        };
        $timeout(() => {
            this.$rootScope.$broadcast("showExecuteCmdResult", "1d0caf1c-c966-4d54-93de-8e703d57cba0");
            this.isOpen = true;
        }, 2000);
    }

    doInitToolbar() {
        this.toolbars = [
            this.toolbarUtils.noneBuilder("icon").iconBuilder('apple-keyboard-command', {}).toValue(),
            this.toolbarUtils.labelBuilder('执行命令').attrBuilder({ flex: "" }).toValue()
        ];
    }

    showSivenav() {
        this.isOpen = !this.isOpen;
    }

    doSubmit($event, form) {
        let promise = this.fxAction.doAction(this.key, this.formData, form);
        let results;

        if (promise) {
            this.isBusy = true;
            promise.then((res) => {
                this.materilUtils.showMsg("执行命令成功！");
                results = res;

                return this.fxAction.getModel(this.key);
            }).then((actionModel) => {
                this.actionModel = actionModel;

                return this.fxAction.doDealResult(actionModel, results, this.executeResult);
            }).then((res) => {
                this.isOpen = true;
                this.$rootScope.$broadcast("showExecuteCmdResult", res.data.cmdid);
            }).finally(() => {
                this.isBusy = false;
            });
        }
    }
}


